<template>
  <div class="banner" @click="handleBannerClick">
    <img class="banner-img" :src="bannerImg" />
    <div class="banner-info">
      <div class="banner-title">{{sightName}}</div>
      <div class="banner-number">
        <span class="iconfont center-icon banner-icon">&#xe634;</span>
        {{gallaryImgs.length}}
      </div>
    </div>
    <CommonFadeAnimation>
      <CommonGallary :imgs="gallaryImgs" v-show="showGallary" />
    </CommonFadeAnimation>
  </div>
</template>

<script>
import CommonGallary from "#common/gallary/Gallary";
import CommonFadeAnimation from "#common/fade/FadeAnimation";
export default {
  name: "Banner",
  components: {
    CommonGallary,
    CommonFadeAnimation
  },
  props: {
    sightName: String,
    gallaryImgs: Array,
    bannerImg: String
  },
  data() {
    return {
      showGallary: false
    };
  },
  methods: {
    handleBannerClick() {
      this.showGallary = !this.showGallary;
    }
  }
};
</script>

<style lang="less" scoped>
.banner {
  position: relative;
  overflow: hidden;
  height: 0;
  padding-bottom: 55%;

  .banner-img {
    width: 100%;
  }

  .banner-info {
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: 0.6rem;
    color: #fff;
    background-image: linear-gradient(
      top,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0.8)
    );

    .banner-title {
      flex: 1;
      font-size: 0.36rem;
      text-shadow: 0 1px 2px rgba(0, 0, 0, 0.7);
      padding: 0.2rem;
    }
    .banner-number {
      margin-top: 0.3rem;
      padding: 0 0.4rem;
      height: 0.4rem;
      line-height: 0.4rem;
      border-radius: 0.2rem;
      background: rgba(0, 0, 0, 0.5);
      font-size: 0.24rem;
      margin-right: 0.3rem;

      .banner-icon {
        font-size: 0.24rem;
      }
    }
  }
}
</style>



