<template>
  <div>
    <div class="search">
      <input type="text" v-model="keyword" class="search-input" placeholder="输入城市名或拼音" />
    </div>
    <div class="search-content" ref="search" v-show="keyword">
      <ul>
        <li
          v-for="item in list"
          :key="item.id"
          class="search-item border-bottom"
          @click="handleCityClick(item.name)"
        >{{item.name}}</li>
        <li class="search-item border-bottom" v-show="hasNoData">没有找到匹配城市</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from "better-scroll";
import { mapMutations } from 'vuex'
export default {
  name: "Search",
  data() {
    return {
      keyword: "",
      list: [],
      timer: null
    };
  },
  props: {
    cities: Object
  },
  methods: {
    handleCityClick(city) {
      this.changeCity(city);
      this.$router.push("/home");
    },
    ...mapMutations(["changeCity"])
  },
  computed: {
    hasNoData() {
      return !this.list.length;
    }
  },
  mounted() {
    this.scroll = new Bscroll(this.$refs["search"], { mouseWheel: true, click: true, tap: true });
  },
  watch: {
    keyword() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      if (!this.keyword) {
        this.list = [];
        return;
      }

      this.timer = setTimeout(() => {
        const result = [];

        for (let key in this.cities) {
          this.cities[key].forEach(value => {
            if (
              value.spell.indexOf(this.keyword) != -1 ||
              value.name.indexOf(this.keyword) != -1
            ) {
              result.push(value);
            }
          });
          this.list = result;
        }
      }, 200);
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../assets/styles/varibles.less";
.search {
  height: 0.72rem;
  padding: 0 0.1rem;
  background: @bgColor;

  .search-input {
    height: 0.62rem;
    line-height: 0.62rem;
    width: 100%;
    text-align: center;
    border-radius: 0.06rem;
    color: #666;
    box-sizing: border-box;
    padding: 0 0.1rem;
  }
}
.search-content {
  overflow: hidden;
  position: absolute;
  top: 1.58rem;
  left: 0;
  right: 0;
  bottom: 0;
  background: #eee;
  z-index: 1;

  ul {
    .search-item {
      line-height: 0.62rem;
      padding-left: 0.2rem;
      color: #666;
      background-color: #fff;
    }
  }
}
</style>



