<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont center-icon">&#xe624;</div>
    </div>
    <input type="text" class="header-search" placeholder="输入城市景点\游玩\主题" />
    <router-link to="/city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont">&#xe64a;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: "HomeHeader",
  computed: {
    ...mapState(['city'])
  }
};
</script>

<style lang="less" scoped>
@import "~#styles/varibles.less";
.header {
  line-height: @headerHeight;
  display: flex;
  background-color: @bgColor;
  .header-left {
    width: 0.64rem;
    float: left;
    color: #fff;

    .center-icon {
      font-size: 0.4rem;
      text-align: center;
    }
  }
  .header-search {
    flex: 1;
    height: 0.62rem;
    margin-top: 0.12rem;
    border-radius: 0.1rem;
    margin-left: 0.2rem;
    padding-left: 0.5rem;
  }
  .header-right {
    min-width: 1.04rem;
    padding: .1rem;
    height: .64rem;
    line-height: .64rem;
    float: right;
    text-align: center;
    color: aliceblue;

    .iconfont {
      font-size: 0.12rem;
    }
  }
}
</style>



