<template>
  <div>
    <ul class="list">
      <li
        class="item"
        v-for="key in letters"
        :key="key"
        :ref="key"
        @click="handleLetterClick"
        @touchmove="handleTouchMove"
        @touchstart.prevent="handleTouchStart"
        @touchend="handleTouchEnd"
      >{{key}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Alphabet",
  props: {
    cities: Object
  },
  data() {
    return {
      touchStatus: false,
      startY: 0,
      timer: null
    };
  },
  updated() {
    this.startY = this.$refs["A"][0].offsetTop;
  },
  computed: {
    letters() {
      const letters = [];
      for (let key in this.cities) {
        letters.push(key);
      }
      return letters;
    }
  },
  methods: {
    handleLetterClick(e) {
      this.$emit("change", e.target.innerText);
    },
    handleTouchStart(e) {
      this.touchStatus = true;
    },
    handleTouchMove(e) {
      if (this.touchStatus) {
        if (this.timer) {
          clearTimeout(this.timer);
        }
        setTimeout(() => {
          const touchY = e.touches[0].clientY - this.startY - 22;
          const index = Math.floor((touchY - this.startY) / 22);
          if (index >= 0 && index < this.letters.length) {
            this.$emit("change", this.letters[index]);
          }
        }, 30);
      }
    },
    handleTouchEnd(e) {
      this.touchStatus = false;
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../assets/styles/varibles.less";
.list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  right: 0;
  top: 1.58rem;
  bottom: 0;
  width: 0.4rem;

  .item {
    text-align: center;
    line-height: 0.44rem;
    color: @bgColor;
  }
}
</style>



