<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(items, index) in pages" :key="index">
        <div class="icon" v-for="item in items" :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl" @click="preventDefault">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "Icons",
  data() {
    return {
      swiperOption: {
        autoPlay: false
      }
    };
  },
  props: {
    iconList: Array
  },
  computed: {
    pages() {
      const pages = [];
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8);
        if (!pages[page]) {
          pages[page] = [];
        }
        pages[page].push(item);
      });
      return pages;
    }
  },
  methods: {
    preventDefault (){
      return false;
    }
  }
};
</script>

<style lang="less">
@import "~#styles/varibles.less";
@import "~#styles/mixins.less";
.swiper-container {
  height: 0;
  padding-bottom: 50%;
}
.icons {
  height: 0;
  padding-bottom: 50%;
  .icon {
    position: relative;
    overflow: hidden;
    width: 25%;
    padding-bottom: 25%;
    height: 0;
    float: left;

    .icon-img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0.44rem;
      box-sizing: border-box;
      padding: 0.1rem;
      .icon-img-content {
        height: 100%;
        display: block;
        margin: 0 auto;
      }
    }
    .icon-desc {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 0.44rem;
      line-height: 0.44rem;
      color: @darkTextColor;
      text-align: center;
      .ellipsis
    }
  }
}
</style>



