<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom">您的位置</div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list">
          <div class="button-wrapper" v-for="item in hotCities" :key="item.id" @click="handleCityClick(item.name)">
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area" v-for="(items, key) in cities" :key="key" :ref="key">
        <div class="title border-topbottom">{{key}}</div>
        <ul class="item-list" v-for="item in items" :key="item.id">
          <li class="item border-bottom" @click="handleCityClick(item.name)">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import {mapState, mapMutations} from 'vuex'
export default {
  name: "List",
  props: {
    cities: Object,
    hotCities: Array,
    letter: String
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city);
      this.$router.push('/home')
    },
    ...mapMutations(['changeCity'])
  },
  mounted (){
    this.scroll = new Bscroll(this.$refs.wrapper,  { mouseWheel: true, click: true, tap: true });
  },
  watch: {
    letter (){
      const element = this.$refs[this.letter][0];
      if(this.letter){
        this.scroll.scrollToElement(element);
      }

    }
  }
};
</script>

<style lang="less" scoped>
.list {
  position: absolute;
  overflow: hidden;
  top: 1.58rem;
  left: 0;
  right: 0;
  bottom: 0;
  .area {
    .border-topbottom::after {
      border-color: #ccc;
    }
    .border-topbottom::before {
      border-color: #ccc;
    }
    .border-bottom::before {
      border-color: #ccc;
    }

    .title {
      height: 0.4rem;
      line-height: 0.4rem;
      padding-left: 0.2rem;
      background-color: #eee;
      color: #666;
      font-size: 0.26rem;
    }

    // 您的位置与热门城市区块
    .button-list {
      overflow: hidden;
      padding: 0.1rem 0.6rem 0.1rem 0.1rem;

      .button-wrapper {
        width: 33.3333%;
        float: left;

        .button {
          margin: 0.1rem;
          padding: 0.1rem 0;
          border: 0.02rem solid #ccc;
          border-radius: 0.06rem;
          text-align: center;
        }
      }
    }

    .item-list {
      .item {
        line-height: 0.76rem;
        padding-left: 0.2rem;
      }
    }
  }
}
</style>



