<template>
  <div class="recommend">
    <div class="title">热销推荐</div>
    <ul>
      <router-link
        tag="li"
        class="item border-bottom"
        v-for="(item, index) in recommendList"
        :key="item.id + index"
        :to="/detail/ + item.id"
      >
        <img class="item-img" :src="item.imgUrl" />
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Recommend",
  props: {
    recommendList: Array
  }
};
</script>

<style lang="less" scoped>
@import "~#styles/mixins.less";
.recommend {
  .title {
    margin-top: 0.2rem;
    line-height: 0.4rem;
    text-indent: 0.2rem;
    background: #eee;
  }
  .item {
    overflow: hidden;
    height: 1.9rem;
    display: flex;

    .item-img {
      width: 1.7rem;
      height: 1.7rem;
      padding: 0.1rem;
    }
    .item-info {
      flex: 1;
      padding: 0.1rem 0;
      min-width: 0;

      .item-title {
        line-height: 0.54rem;
        font-size: 0.32rem;
        .ellipsis;
      }

      .item-desc {
        line-height: 0.4rem;
        color: #ccc;
        .ellipsis;
      }

      .item-button {
        line-height: 0.44rem;
        margin-top: 0.16rem;
        color: #fff;
        background-color: #ff9300;
        padding: 0 0.2rem;
        border-radius: 0.06rem;
      }
    }
  }
}
</style>



