<template>
  <div class="recommend">
    <div class="title">周末去哪儿</div>
    <ul>
      <li class="item border-bottom" v-for="(item, index) in weekendList" :key="item.id + index">
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" />
        </div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Weekend",
  props: {
    weekendList: Array
  }
};
</script>

<style lang="less" scoped>
@import "~#styles/mixins.less";
.recommend {
  .title {
    line-height: 0.8rem;
    text-indent: 0.2rem;
    background: #eee;
  }
  .item {

    .item-img-wrapper {
      overflow: hidden;
      height: 0;
      padding-bottom: 38.0080%;
      .item-img {
        width: 100%;
      }
    }
    .item-info {
      padding: .15rem;
      .item-title {
        line-height: 0.54rem;
        font-size: 0.32rem;
        .ellipsis;
      }

      .item-desc {
        line-height: 0.4rem;
        color: rgb(124, 122, 122);
        .ellipsis;
      }
    }
  }
}
</style>



